  --primary-background-color: #fff;
  --footer-background-color: #fefefe;
  --primary-text-color: #2c2c2c;
  --invalid: #ff705d;
  --shadow-fruit-color: radial-gradient( 50% 50% at 50% 50%, #2f583f 0%, rgba(105, 105, 105, 0) 100% );
  --border-primary-color: #ffe550;
  --cubic-bezer: cubic-bezier(.4, 0, .2, 1);
}

